<template>
  <div>
    <v-header>
      <h1 slot="title">身体状况</h1>
    </v-header>
      <form action="">
        <mt-field label="姓名" v-model="name" disabled="disabled"></mt-field>
      <mt-field label="性别" v-model="sex" disabled="disabled"></mt-field>
      <mt-field label="生日" v-model="birthday" disabled="disabled"></mt-field>
      <mt-field label="体温" v-model="temperature" disabled="disabled"></mt-field>
      <mt-field label="呼吸" v-model="breathing" disabled="disabled"></mt-field>
      <mt-field label="血压 左" v-model="left" disabled="disabled" state="warning"></mt-field>
      <mt-field label="血压 右" v-model="right" disabled="disabled" state="warning"></mt-field>
      <mt-field label="听力" v-model="hearing" disabled="disabled"></mt-field>
      <mt-field label="心脏" v-model="heart" disabled="disabled"></mt-field>
      </form>  
      <v-footer></v-footer>
  </div>
</template>

<script>
  import Header from '@/common/_header.vue'
  import Footer from '@/common/_footer.vue'
  import {
    Field
  } from 'mint-ui';
  export default {
    components:{
      'v-header':Header,
      'v-footer':Footer
    },
    data() {
      return {
        name: '张东升',
        sex: '男',
        birthday: '1960/08/01',
        temperature: '36.6℃',
        breathing: '40 次/分',
        left: '159mmHg (偏高)',
        right: '149mmHg (偏高)',
        hearing: '正常',
        heart: '70 次/分'
      }
    },
  }

</script>

<style>

</style>



